const initialState = {
  data: [],
  MorePosts: [],
  product: [],
  comments: [],
  replies: [],
  counter: 0,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case "add new data":
      return { ...state, data: action.data };
    case "more posts":
      return { ...state, MorePosts: action.moreposts };
    case "get product by id":
      return {
        ...state,
        product: state.data.filter((item) => item.node.id === action.id),
      };
    case "comments":
      return {
        ...state,
        comments: action.comments,
      };
    case "replies":
      return {
        ...state,
        replies: action.replies,
      };
    case "incrementCounter":
      return { ...state, counter: state.counter + 1 };
    case "decrementCounter":
      return { ...state, counter: state.counter - 1 };
    case "resetCounter":
      return { ...state, counter: 0 };
    default:
      return state;
  }
};
export default reducer;
