import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import {
  Node,
  Avatar,
  Tagline,
  Vote,
  Title,
  Container,
  NodeBody,
  VotesCount,
  I,
  CommentsCount,
  CommentIcon,
  Loader,
  LoaderContainer,
} from "../Posts/style";
import { MorePostsContainer } from "./style";
// const MorePosts = (props) => {
//   return (
//     <>
{
  /* <div>this is more posts component </div>
      <div>
        the cursor and name of the last post is: {props.data.cursor}{" "}
        {props.data.node.name}
      </div> */
}

class MorePosts extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    fetch("https://api.producthunt.com/v2/api/graphql", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer f20AQz3Ap7trjQyQESkDK655LXGSYgTDd0FElcccaJA",
      },
      body: JSON.stringify({
        query: `{
                posts (after:"MjA="){
                  edges {
                    cursor
                    node {
                      id
                      name
                      description 
                      votesCount
                      commentsCount
                      website
                      tagline
                      thumbnail {
                        url
                      }
                      media {
                        videoUrl
                        type
                        url
                      }
                    }
                  }
                }
              }`,
        variables: null,
      }),
    })
      .then((response) => response.json())
      .then((data) => this.props.morePosts(data.data.posts.edges));
  }
  render() {
    const { MorePosts } = this.props;
    return (
      <MorePostsContainer>
        {MorePosts.length > 0 &&
          MorePosts.map((item) => (
            <div
              to={`/${item.node.id}`}
              key={item.node.id}
              style={{ textDecoration: "none" }}
            >
              <Node>
                <Avatar src={item.node.thumbnail.url} />
                <NodeBody>
                  <Title>{item.node.name}</Title>
                  <Tagline>{item.node.tagline}</Tagline>
                  <CommentsCount>
                    <CommentIcon className="fas fa-comment"></CommentIcon>
                    {item.node.commentsCount}
                  </CommentsCount>
                </NodeBody>
                <Vote>
                  <VotesCount>
                    <I className="fas fa-caret-up" />
                    {item.node.votesCount}
                  </VotesCount>
                </Vote>
              </Node>
            </div>
          ))}
      </MorePostsContainer>
    );
  }
}

//     </>
//   );
// };
export default withRouter(MorePosts);
