// export default withRouter(Replies);

class Replies extends React.Component {
  constructor(props) {
    super(props);
    this.state = { replies: [] };
  }
  componentDidMount() {
    fetch("https://api.producthunt.com/v2/api/graphql", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer f20AQz3Ap7trjQyQESkDK655LXGSYgTDd0FElcccaJA",
      },
      body: JSON.stringify({
        query: `{
            comment(id:${this.props.commentId}){
                  replies{
                    edges{
                      node{
                        id
                        body
                        user{
                          name
                          profileImage
                          isMaker
                        }
                      }
                    }
                  }
                }
              }`,
        variables: null,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        // console.log("in fetch =>" + data.data.comment.replies.edges.length);
        this.setState({ replies: data.data.comment.replies.edges });
        // this.props.addReplies(data.data.comment.replies.edges);
      });
  }

  render() {
    const { replies } = this.state;
    return (
      <>
        {replies.length > 0 &&
          replies.map((item) => (
            <Replie key={item.node.id}>
              <Header>
                <Img src={item.node.user.profileImage} />
                <H1>{item.node.user.name}</H1>
              </Header>
              <Pre>
                <Description>{item.node.body}</Description>
              </Pre>
            </Replie>
          ))}
        {console.log("replies update")}
      </>
    );
  }
}
export default Replies;
