import { connect } from "react-redux";
import { morePosts } from "../Action";
import MorePosts from "../MorePosts";

const mapStateToProps = (state) => {
  return {
    MorePosts: state.MorePosts,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    morePosts: (moreposts) => dispatch(morePosts(moreposts)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MorePosts);
