import React, { Component } from "react";
import {
  Container,
  Header,
  Media,
  Description,
  Avatar,
  Previous,
  Next,
  Body,
  H1,
  P,
  Img,
  Titles,
  Discussion,
} from "./style";
import { withRouter, Switch, Route, Link } from "react-router-dom";
import ReactPlayer from "react-player";
import CommentsList from "../Comments";

class Product extends Component {
  componentDidMount() {
    this.props.getProductById(this.props.match.params.id);

    fetch("https://api.producthunt.com/v2/api/graphql", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer f20AQz3Ap7trjQyQESkDK655LXGSYgTDd0FElcccaJA",
      },
      body: JSON.stringify({
        query: `{
          post(id:${this.props.match.params.id}){
            comments{
              edges{
                node{
                  id
                  body
                  votesCount
				          createdAt
                  user{
                    name
                    profileImage
                    isMaker
                  }
                }
              }
            }
          }
              }`,
        variables: null,
      }),
    })
      .then((response) => response.json())
      .then((data) =>
        this.props.addComments(data.data.post.comments.edges.reverse())
      );
    // fetch("https://api.producthunt.com/v2/api/graphql", {
    //   method: "POST",
    //   headers: {
    //     Accept: "application/json",
    //     "Content-Type": "application/json",
    //     Authorization: "Bearer f20AQz3Ap7trjQyQESkDK655LXGSYgTDd0FElcccaJA",
    //   },
    //   body: JSON.stringify({
    //     query: `{
    //       post(id:${this.props.match.params.id}){
    //       comments{
    //         edges{
    //           node{
    //            replies{
    //               edges{
    //                 node{
    //                     id
    //                 }
    //               }
    //               }
    //             }
    //           }
    //         }
    //       }
    //   }`,
    //     variables: null,
    //   }),
    // })
    //   .then((response) => response.json())
    //   .then((data) => this.props.addReplies(data.data.post.comments.edges));
  }

  getNext = () => {
    this.props.counter !== this.props.productData[0].node.media.length - 1 &&
      this.props.incrementCounter();
  };
  getPrevious = () => {
    this.props.counter !== 0 && this.props.decrementCounter();
  };

  render() {
    const { productData, comments, counter } = this.props;
    let productMedia = [];

    productData.length > 0 &&
      (productData[0].node.media = productData[0].node.media.filter(
        (item) => item.url !== productData[0].node.thumbnail.url
      ));
    productData.length > 0 && (productMedia = productData[0].node.media);
    return (
      <Container>
        <Media>
          <Previous
            onClick={this.getPrevious}
            className="fas fa-chevron-left"
            counterIs={counter}
          />
          <Next
            onClick={this.getNext}
            className="fas fa-chevron-right"
            counterIs={counter}
          />
          {productMedia.length > 0 &&
            (productMedia[counter].type === "image" ? (
              <Avatar src={productMedia[counter].url} />
            ) : (
              <ReactPlayer
                url={productMedia[counter].videoUrl}
                controls
                style={{ maxHeight: "-webkit-fill-available" }}
              />
            ))}
        </Media>
        {productData.length > 0 && (
          <Body>
            <Header>
              {productData[0].node.thumbnail && (
                <Img src={productData[0].node.thumbnail.url} />
              )}
              <Titles>
                <H1>{productData[0].node.name}</H1>
                <P>{productData[0].node.tagline}</P>
              </Titles>
            </Header>
            <Description>{productData[0].node.description}</Description>
          </Body>
        )}

        {/* <div>
          <Link to={`${this.props.match.url}/morePosts`}>More posts</Link>
        </div>
        <Switch>
          <Route exact path={`${this.props.match.url}/morePosts`}>
            <div>yeeeeeessssss</div>
          </Route>
        </Switch> */}

        <Discussion>discussion</Discussion>
        <CommentsList comments={comments} />
      </Container>
    );
  }
}
export default withRouter(Product);
