import React, { Component } from "react";
import { Link, withRouter, Switch, Route } from "react-router-dom";
import {
  Node,
  Avatar,
  Tagline,
  Vote,
  Title,
  Container,
  NodeBody,
  VotesCount,
  I,
  CommentsCount,
  CommentIcon,
  MorePostsDiv,
  Loader,
  LoaderContainer,
} from "./style";

import MorePosts from "../Connecters/MorePostsConnect";

class Posts extends Component {
  constructor(props) {
    super(props);
    this.state = { counter: 0 };
  }

  Test = () => {
    let arr = [];
    for (let i = 0; i < this.state.counter; i++) {
      arr.push(<MorePosts key={i} />);
    }
    console.log(this.state.counter);
    return arr;
  };

  componentDidMount() {
    fetch("https://api.producthunt.com/v2/api/graphql", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer f20AQz3Ap7trjQyQESkDK655LXGSYgTDd0FElcccaJA",
      },
      body: JSON.stringify({
        query: `{
                posts {
                  edges {
                    cursor
                    node {
                      id
                      name
                      description
                      votesCount
                      commentsCount
                      website
                      tagline
                      thumbnail {
                        url
                      }
                      media {
                        videoUrl
                        type
                        url
                      }
                    }
                  }
                }
              }`,
        variables: null,
      }),
    })
      .then((response) => response.json())
      .then((data) => this.props.addNewData(data.data.posts.edges));
    this.props.resetCounter();
  }
  render() {
    const { data } = this.props;
    return (
      <Container>
        {/* this is posts component */}
        {data.length === 0 && (
          <LoaderContainer>
            <Loader />
          </LoaderContainer>
        )}
        {data.map((item) => (
          <Link
            to={`/${item.node.id}`}
            key={item.node.id}
            style={{ textDecoration: "none" }}
          >
            <Node>
              <Avatar src={item.node.thumbnail.url} />
              <NodeBody>
                <Title>{item.node.name}</Title>
                <Tagline>{item.node.tagline}</Tagline>
                <CommentsCount>
                  <CommentIcon className="fas fa-comment"></CommentIcon>
                  {item.node.commentsCount}
                </CommentsCount>
              </NodeBody>
              <Vote>
                <VotesCount>
                  <I className="fas fa-caret-up" />
                  {item.node.votesCount}
                </VotesCount>
              </Vote>
            </Node>
          </Link>
        ))}
        <Switch>
          <Route exact path="/home/morePosts">
            {this.Test()}
            {/* <MorePosts data={data[data.length - 1]} /> */}
          </Route>
        </Switch>
        <MorePostsDiv>
          <Link
            to={`${this.props.match.url}/morePosts`}
            onClick={() => this.setState({ counter: this.state.counter + 1 })}
            style={{ textDecoration: "none", color: "white" }}
          >
            More posts
          </Link>
        </MorePostsDiv>
        {console.log("posts update")}
      </Container>
    );
  }
}
export default withRouter(Posts);
